import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import './Home.css';
// import MembershipModal from './MembershipModal';

const MembershipModal = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <motion.div 
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50"
    >
      <motion.div 
        initial={{ y: 50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        exit={{ y: 50, opacity: 0 }}
        className="bg-white rounded-lg w-full max-w-4xl p-6 mx-4"
      >
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-3xl font-bold text-gray-800">Join BILVINE'S ENERGYM!</h2>
          <button 
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700"
          >
            ×
          </button>
        </div>

        <div className="grid md:grid-cols-3 gap-6">
          {/* Basic Plan */}
          <motion.div 
            whileHover={{ scale: 1.05 }}
            className="border rounded-lg p-6 hover:shadow-xl transition-shadow"
          >
            <h3 className="text-xl font-bold mb-4">Basic Plan</h3>
            <p className="text-3xl font-bold mb-4">$29.99<span className="text-sm font-normal">/month</span></p>
            <ul className="space-y-2 mb-6">
              <li>✓ Access to gym equipment</li>
              <li>✓ Basic fitness assessment</li>
              <li>✓ Locker room access</li>
              <li>✓ Water fountain access</li>
            </ul>
            <button className="w-full bg-blue-600 text-white py-2 rounded-lg hover:bg-blue-700">
              Select Plan
            </button>
          </motion.div>

          {/* Premium Plan */}
          <motion.div 
            whileHover={{ scale: 1.05 }}
            className="border rounded-lg p-6 hover:shadow-xl transition-shadow bg-blue-50"
          >
            <div className="absolute top-0 right-0 bg-blue-600 text-white px-2 py-1 text-sm rounded-bl">
              Popular
            </div>
            <h3 className="text-xl font-bold mb-4">Premium Plan</h3>
            <p className="text-3xl font-bold mb-4">$49.99<span className="text-sm font-normal">/month</span></p>
            <ul className="space-y-2 mb-6">
              <li>✓ All Basic Plan features</li>
              <li>✓ Personal trainer (2x/month)</li>
              <li>✓ Group fitness classes</li>
              <li>✓ Nutrition consultation</li>
              <li>✓ Towel service</li>
            </ul>
            <button className="w-full bg-blue-600 text-white py-2 rounded-lg hover:bg-blue-700">
              Select Plan
            </button>
          </motion.div>

          {/* Elite Plan */}
          <motion.div 
            whileHover={{ scale: 1.05 }}
            className="border rounded-lg p-6 hover:shadow-xl transition-shadow"
          >
            <h3 className="text-xl font-bold mb-4">Elite Plan</h3>
            <p className="text-3xl font-bold mb-4">$79.99<span className="text-sm font-normal">/month</span></p>
            <ul className="space-y-2 mb-6">
              <li>✓ All Premium Plan features</li>
              <li>✓ Unlimited personal training</li>
              <li>✓ Private locker</li>
              <li>✓ Spa access</li>
              <li>✓ Recovery zone access</li>
              <li>✓ Priority class booking</li>
            </ul>
            <button className="w-full bg-blue-600 text-white py-2 rounded-lg hover:bg-blue-700">
              Select Plan
            </button>
          </motion.div>
        </div>

        <div className="mt-8 bg-gray-50 p-6 rounded-lg">
          <h3 className="text-xl font-bold mb-4">Additional Amenities</h3>
          <div className="grid md:grid-cols-3 gap-4">
            <div>
              <h4 className="font-bold mb-2">Equipment</h4>
              <ul className="space-y-1 text-sm">
                <li>• State-of-the-art cardio machines</li>
                <li>• Free weights area</li>
                <li>• Functional training zone</li>
              </ul>
            </div>
            <div>
              <h4 className="font-bold mb-2">Classes</h4>
              <ul className="space-y-1 text-sm">
                <li>• Yoga and Pilates</li>
                <li>• HIIT workouts</li>
                <li>• Spinning classes</li>
              </ul>
            </div>
            <div>
              <h4 className="font-bold mb-2">Facilities</h4>
              <ul className="space-y-1 text-sm">
                <li>• Sauna and steam room</li>
                <li>• Shower facilities</li>
                <li>• Parking space</li>
              </ul>
            </div>
          </div>
        </div>
      </motion.div>
    </motion.div>
  );
};

function Home() {
  const [username, setUsername] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);

  useEffect(() => {
    const storedUsername = localStorage.getItem('username');
    if (storedUsername) {
      setUsername(storedUsername);
    }
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('email');
    localStorage.removeItem('password');
    localStorage.removeItem('username');
    setUsername('');
  };

  return (
    <div className="main">
      <div className="page1">
        <motion.h1 
          initial={{ scale: 0 }}
          animate={{ scale: 1 }}
          transition={{ duration: 0.5 }}
        >
          I CAN!
        </motion.h1>
        <div className="top">
          <div className="header container">
            <motion.div 
              className="logo"
              initial={{ x: -100, opacity: 0 }}
              animate={{ x: 0, opacity: 1 }}
              transition={{ duration: 0.5 }}
            >
              <h2>BILVINE'S ENERGYM<span>!</span></h2>
            </motion.div>
            <div className="nav-link">
              <ul>
                <motion.li 
                  initial={{ y: -50, opacity: 0 }}
                  animate={{ y: 0, opacity: 1 }}
                  transition={{ delay: 0.2 }}
                >
                  <Link to="/" className="link">HOME</Link>
                </motion.li>
                <motion.li 
                  initial={{ y: -50, opacity: 0 }}
                  animate={{ y: 0, opacity: 1 }}
                  transition={{ delay: 0.3 }}
                >
                  <Link to="/merchandise" className="link">Merchandise</Link>
                </motion.li>
                <motion.li 
                  initial={{ y: -50, opacity: 0 }}
                  animate={{ y: 0, opacity: 1 }}
                  transition={{ delay: 0.4 }}
                >
                  <Link to="/shop" className="link">Shop</Link>
                </motion.li>
                <motion.li 
                  initial={{ y: -50, opacity: 0 }}
                  animate={{ y: 0, opacity: 1 }}
                  transition={{ delay: 0.5 }}
                >
                  <Link to="/support" className="link">Support</Link>
                </motion.li>
              </ul>
            </div>
            <div className="back">
              {username ? (
                <div>
                  <span>Welcome, {username}</span>
                  <button className="btn" onClick={handleLogout}>Logout</button>
                </div>
              ) : (
                <Link to="/login" className="btn">Login</Link>
              )}
            </div>
          </div>
          <motion.div 
            className="content"
            initial={{ y: 100, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.8 }}
          >
            <h2>"Look in the mirror. That's your competition"</h2>
            <h3>ABHISHEK DHIMAN</h3>
          </motion.div>
          <div className="scorpio">
            {/* Your existing scorpio content */}
          </div>
        </div>
        <div className="footer-up">
          <motion.div 
            className="image"
            initial={{ scale: 0.8, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ duration: 0.8 }}
          >
            <img src="/assets/images/gym3.png" alt="Gym" />
          </motion.div>
          <motion.div 
            className="footer-content"
            initial={{ x: 100, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            transition={{ duration: 0.8 }}
          >
            <p>
              "Embark on a transformative fitness journey with our state-of-the-art gym, 
              where passion meets purpose, and every workout is a step towards a healthier, 
              stronger you."
            </p>
            <motion.button 
              className="btn"
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
              onClick={() => setIsModalOpen(true)}
            >
              JOIN US!
            </motion.button>
          </motion.div>
          <motion.div 
            className="About"
            initial={{ y: 50, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.8 }}
          >
            <Link to="/diet"><span>DIET PLANS</span></Link>
            <Link to="/workout"><span>WORKOUT PLAN</span></Link>
            <Link to="/expert-advice"><span>EXPERT ADVICE</span></Link>
            <Link to="/fat-to-fit"><span>FAT TO FIT</span></Link>
          </motion.div>
        </div>
      </div>
      <MembershipModal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)} />
    </div>
  );
}

export default Home;