import React, { useState } from "react";
import "./DietPlans.css";

function DietPlans() {
  const [selectedPlan, setSelectedPlan] = useState(null);

  const dietPlans = {
    muscleGain: {
      title: "Muscle Gain Diet",
      description:
        "A high-protein diet to build muscle mass. Includes lean meats, eggs, dairy, and healthy fats.",
      meals: [
        "🍳 Breakfast: Scrambled eggs with whole wheat toast & avocado",
        "🥗 Lunch: Grilled chicken with quinoa & steamed broccoli",
        "🍗 Dinner: Salmon with brown rice & sautéed spinach",
        "🥜 Snacks: Protein shake, almonds, and Greek yogurt",
      ],
    },
    fatLoss: {
      title: "Fat Loss Diet",
      description:
        "A low-carb, high-fiber meal plan to reduce body fat and maintain energy levels.",
      meals: [
        "🥑 Breakfast: Oatmeal with chia seeds & berries",
        "🥗 Lunch: Grilled turkey with mixed greens & olive oil dressing",
        "🍲 Dinner: Stir-fried tofu with vegetables & cauliflower rice",
        "🍏 Snacks: Green smoothie, walnuts, and boiled eggs",
      ],
    },
    balanced: {
      title: "Balanced Diet",
      description:
        "A nutritious mix of carbs, proteins, and healthy fats for overall well-being.",
      meals: [
        "🍎 Breakfast: Greek yogurt with granola & honey",
        "🥙 Lunch: Whole grain sandwich with lean turkey & avocado",
        "🥘 Dinner: Grilled fish with quinoa & roasted vegetables",
        "🌰 Snacks: Hummus with carrots, almonds, and a banana",
      ],
    },
  };

  return (
    <div className="diet-plans">
      <h1>Diet Plans</h1>
      <p>Choose a diet plan tailored for your fitness goals.</p>

      <div className="diet-cards">
        {Object.keys(dietPlans).map((key) => (
          <div key={key} className="diet-card">
            <h2>{dietPlans[key].title}</h2>
            <p>{dietPlans[key].description}</p>
            <button onClick={() => setSelectedPlan(dietPlans[key])}>
              View Plan
            </button>
          </div>
        ))}
      </div>

      {selectedPlan && (
        <div className="popup active">
          <div className="popup-content">
            <h2>{selectedPlan.title}</h2>
            <p>{selectedPlan.description}</p>
            <ul>
              {selectedPlan.meals.map((meal, index) => (
                <li key={index}>{meal}</li>
              ))}
            </ul>
            <button className="close-btn" onClick={() => setSelectedPlan(null)}>
              Close
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default DietPlans;
